* { touch-action: none; }
.homelist{
    background: #ccc;
    .homelist-cell{
        position: relative;
        height: 10.83rem;
        .homelist-bck{
        width: 100%;
        height: 10.83rem;
        margin-bottom: .2rem;
        img{
            height: 10.83rem;
            width: 100%;
            cursor: pointer;
        }
    }
        .homelist-cnt{
            position: absolute;
            left: 40%;
            top: 0;
            height: 100%;
            
            .cnt-top{
                margin-top: 1.4rem;
                color: #333;
                font-size: 13px;
                line-height: 17px;
                max-height: 51px;
                overflow: hidden;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 3;
                margin-bottom: 5px;
            }
            .cnt-wrap{
                margin-top: 2.5rem;
                font-size: 14px;
                span:nth-child(1){
                    font-size: 16px;
                    color: #fe4070;
                    padding-right: .2rem;
                }
                span:nth-child(2){
                    color:#999;
                    font-size: 10px;

                }
                span:nth-child(3){
                    display: block;
                    font-size: 12px;
                    color:#999;
                }
            }
        }
    }
    
    
}