* { touch-action: none; }
.user{
    background: #eaeaea;
    .user-header{
        background-image: linear-gradient(-204deg,#fd465f 0,#fc5e9f 100%);
        height: 15.3rem;
        width: 100%;
        margin-bottom: .6rem;
        color:white;
        font-weight: 700;
        img{
        width:5rem;height:5rem;display: block;margin: 2rem auto;border-radius: 5px;
        }
        .user-header-btn{
            margin: 1rem auto;display: flex;justify-content: center;color:#fff;font-weight: 700;text-align: center;font-size: 18px;
            .gologin{
                width: 8rem;background: none;border: none;display: block;font-weight: 700;color:#fff
            }
            .goreg{
                width: 8rem;background: none;border: none;display: block;font-weight: 700;color:#fff
            }
        }
    }
    .user-block-title{
        display: flex;justify-content: space-between ;background: white;padding-left:8px;
        border-bottom: 1px solid #eaeaea;
        line-height: 3rem;
        font-size: 13px;
        height: 3rem;
        width: 100%;
        .left{
            i{width: 1.67rem;height: 1.67rem;margin-right: 1rem;}
        }
        .right{
            color: #999;
            font-size: 12px;
            margin-right: .5rem;
            i{width: 1.67rem;height: 1.67rem;margin-right: 1rem;font-size: 12px}

        }
    }
    .user-block-content{
        height: 5.8rem;background: white;margin-bottom: .6rem;width: 100%;display: flex;
        text-align: center;
        justify-content: space-around;
        align-items: center;
        .grid{
            i{display: block;width: 2rem;height: 2rem;margin: 5px auto;font-size: 24px;color:#666}
            span{display: block;font-size: 12px;color:#666}
        }
    }
    .user-block-money{
        height: 3rem;background: white;border-bottom: 1px solid #eaeaea;
        padding-left: 8px;display:flex;
        i{display: block;font-size: 24px;color:#666;width: 1.6rem;height: 1.6rem;margin-right: 1.5rem;line-height: 3rem;}
        span{font-size: 16px;color:#666;display: block;line-height: 3rem;}
    }
    .user-block-activity{
        height: 4rem;width: 100%;margin-bottom: .8rem;display: flex;justify-content: space-between;
        background: white;text-align:center;
        span{width: 25%;font-size: 12px;line-height: 4rem;display: block;color: #666;}
    }
    .user-block-chat{
        height: 15rem;background: white;border-bottom: 1px solid #eaeaea;
        padding-left: 1rem;
        ul{
            margin: 0;padding:0;
            li{
                display: flex;
                i{display: block;font-size: 24px;width: 1.6rem;height: 1.6rem;margin-right: 1.5rem;line-height: 3rem;}
                span{font-size: 16px;color:#666;display: block;line-height: 3rem;}
            }
        }
        
    }
}
