* { touch-action: none; }
* { touch-action: none; }
.login{
    height: 3.67rem;border-bottom:1px solid #ccc;
    line-height: 3.67rem;color:#999;
    i:nth-child(1){font-size: 24px;display: block;text-align: left}
    span{width: 100%;display: block;text-align: center;font-size: 17px;}
    .am-flexbox-item{
        .icon{
            text-align: right;margin-right: 10px;font-style:normal;
            font-size: 14px;color:#666;
        }
    }
    .login-use{
        height:5rem;text-align: center;margin-top: 6rem;color:#999;font-size: 14px;
    }
    .login-ipt{
        margin: 0 12%;
        margin-top: 2rem;
        input{margin-bottom: 1.5rem; text-indent: 21px;width:100%;font-size: 14px;background: rgb(231, 229, 229);border-radius: 20px;border: none;color:#999;height: 3rem;text-align: left}
        }
    .login-btn{
        margin: 0 12%;margin-top: 2rem;
    }
    .login-agree{
        color:#999;height: 3rem;line-height: 3rem;margin: 0 auto;
        span{font-size: 12px;}
    }
}