* { touch-action: none; }
.shopcar{
    .tip{
        span{float: left;}
        .button{
            button{color:#fff; border: none;height: 1.5rem;width: 3.5rem;margin-left: .5rem;border-radius: 5px;background: #b81c22}
            float:right;text-align: center
        }
        height: 2.2rem;font-size:12px;width: 80%;margin: 0 auto;line-height: 2.2rem;
    }
    .shop-list{
                display: flex;justify-content: space-around;align-items: center;width: 100%;margin:1rem 3%;height: 8.67rem;
                img{width: 7rem;height: 7rem;;}
                .check{width:1.7rem;height: 1.7rem;}
                .shop-detail{
                    p{color:#333;font-size: 12px;max-width: 19rem;line-height: 1.5rem}
                    .price{color:#fe4070;font-size: 12px;display: block;margin: .3rem 0}
                    .des{font-size: 10px;color:#ccc;}
                    .count{
                        button{
                            width: 1.8rem;height: 1.8rem;border-radius: 50%;border: 1px solid #ccc; background: #eee
                        }
                        input{width: 2rem;text-align: center;border:1px solid #ccc;margin: 0 .5rem;}
                    }
                }
         
    }
    .shop-settlement{
        position: fixed ;width: 100%;border-top: 1px solid #eee;height: 4rem;bottom: 4.12rem;
        display: flex;justify-content: space-around;align-items: center;
        .setprice{
            display: flex;
            p{font-size: 14px;color: #999;}
            span{
                font-size:14px;color:#fe4070;display: block;line-height: 4rem;
            }
        }
        .set{
            line-height: 3rem; width: 11rem;font-size: 14px; height: 3rem;background: #fe4070;color:#fff;text-align: center;
            border-radius: 10px;
        }
    }
    
    .no-shop{
        flex:1;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        height: 47rem;;
        .title{
            font-size: .875rem;
            line-height: .9375rem;
            color: #333;
            margin-top: 1rem;
            padding: 0 .75rem;
            text-align: center;
        }
        .gostore{
            width: 7.8125rem;
            height: 2.25rem;
            border-radius: 1.25rem;
            color: #fff;
            background: #fe4070;
            line-height: 2.25rem;
            text-align: center;
            margin-top: 1.4375rem;
            font-size: .8125rem;
        }
    }
}