* { touch-action: none; }
html{
    font-size: 12px;
    .detail{ 
        .detail-show{
            img{
                width: 100%;
                height:31.25rem ;
                margin: 0;
            
            }
        }
        .detail-display{ 
            padding-left: 0.64rem;
            border-bottom: 1px solid #eee;

            padding-right: 0.64rem;
            background: white;
            padding-top: 0.8rem;
            margin-top:-.1rem;
            height: 8rem;
            
            .detail-price{
                height: 24px;
                display: flex;
                justify-content: space-between;
                .left-price{
                    span{
                        font-size: 20px;
                        color:#F33873;
                    }
                    i{
                        font-style: normal;
                        font-size: 12px;
                        margin-left: .5rem;
                        color:#666;
                        text-decoration: line-through;
                    }
                }
                .right-price{
                    span{
                        font-size: 14px;
                        color:#666;

                    }
                }
            }
            .detail-time{
                color:#666;
                font-size: 14px;
                margin-top: 1rem;
            }
            .detail-des{
                font-size: 14px;
                margin-top: .5rem;
            }
            
        }
        .deatil-city{
            padding-top: .6rem;
            background: white;
            ul{
                padding: 0;
                margin: 0;
                li{
                    display: flex;
                    justify-content: space-between;
                    padding: 0 5%;
                    border-bottom: 1px solid #eee;
                    align-items: center;
                    height: 3.2rem;
                    line-height: 3.2rem;
                    .city-address{
                        span{
                            width: 33%;
                            font-size: 14px;
                            color:#666;
                        }
                    }
                    .city-des{
                        text-align: left;
                        width:80%;
                        display: flex;
                        justify-content: space-between;
                        span{
                            // margin-right: 5rem;
                            line-height: 3.2rem;
                        }
                        .icon-shouhuodizhi{
                            color:#F33873;
                        }
                    }
                }
            }
        }
        .deatil-addshop{
            display: flex;
            position: fixed;
            bottom: 0;
            z-index: 1000;
            height: 4rem;
            width: 100%;
            background: white;
            text-align: center;
            align-items: center;
           .go-store{
               width: 20%;
               i{
                   font-size: 24px;
                   color:#666;
               }
               span{
                   display: block;
                   color:#999;
               }
           }
           .add-store{
                width: 20%;
                i{
                    font-size: 24px;
                    color:#666;
                }
                span{
                    display: block;
                    color:#999;
                }
           }
            span:nth-child(3){
                width: 30%;
                height: 100%;
                display: block;
                background: linear-gradient(210deg, #ffcfdf 0%, #fff1f6 98%);
                color: #FE4071;
                line-height: 4rem;
                font-size: 14px;
            }
            span:nth-child(4){
                width: 30%;
                height: 100%;
                display: block;
                background: linear-gradient(to right, #f779a3 0%, #fe4070 100%);
                color: #fff;
                line-height: 4rem;
                font-size: 14px;

            }
        }
    }
}

