* { touch-action: none; }
.speedlist{
    background: #fff;
    position: relative;
    border-bottom: 1px solid #eee;
    padding: .64rem 0;
    display: flex;

    .speedlist-bck{
        height: 6.25rem;
        align-items: center;
        img{
            
            height: 6.25rem;
            width: 6.25rem;
            cursor: pointer;
        }
    }
    .speedlist-cnt{
        height: 100%;
        margin-left: .64rem;
        margin-top: .64rem;
        .cnt-top{
            color: #333;
            font-size: 14px;
            line-height: 20px;
            height: 40px;
            word-break: break-all;
            word-wrap: break-word;
            overflow: hidden;
            display: -webkit-box;
        }
        .cnt-wrap{
            margin-top: 2rem;
            font-size: 14px;
            span:nth-child(1){
                font-size: 16px;
                color: #fe4070;
                padding-right: .2rem;
            }
            span:nth-child(2){
                color:#999;
                font-size: 10px;

            }
            
        }
    }
    
}